          { text: 'Join date', value: 'join' },
          { text: 'Credit Gift', value: 'checkbox' },
        ],
        dialog: false,
      }
    },

    methods: {
      resetInput() {
        let inputCheckbox = document.querySelectorAll('.table_head .v-input--selection-controls__input input')

        inputCheckbox.forEach(element => {
          if (element.checked) {
            element.click()
          }
        })

        this.search = ''
        this.sortBy = ''
      },
      showDialog () {
        this.dialog = true
      },
      closeDialog () {
        this.dialog = false
      },
    },
  }
</script>
